import { Canvas } from "../Canvas";
import { useSharedState } from "../context";
import { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";

export const GameScreen = () => {
  const [state, setState] = useSharedState();

  const [height, setHeight] = useState(0);

  useEffect(() => {
    const timeout = setTimeout(() => {
      setHeight(window.innerHeight);
      clearTimeout(timeout);
    }, 200);
  }, [state.fullScreen]);

  const checkDirection = (currentX: number, currentY: number): string => {
    const diffX = Math.abs(state.startX - currentX);
    const diffY = Math.abs(state.startY - currentY);
    const diff = Math.abs(diffX - diffY);

    if (diff >= 20) {
      if (diffX > diffY) {
        if (currentX < state.startX - 10) return "LEFT";
        if (currentX > state.startX + 10) return "RIGHT";
      } else {
        if (currentY < state.startY - 10) return "UP";

        if (currentY > state.startY + 10) return "DOWN";
      }
    }
    return "NULL";
  };

  const handlePointerDown = (e: any) =>
    setState((prev) => ({
      ...prev,
      startX: e.clientX,
      startY: e.clientY,
      draw_controls: true,
    }));

  const handlePointerUp = () =>
    setState((prev) => ({
      ...prev,
      direction: "NULL",
      draw_controls: false,
    }));

  const handlePointerMove = (e: any) =>
    setState((prev) => ({
      ...prev,
      direction: checkDirection(e.clientX, e.clientY),
      currentX: e.clientX,
      currentY: e.clientY,
    }));

  const handlePointerCancel = () => 0;
  return (
    <Box>
      <Button
        color="primary"
        variant="contained"
        onClick={() => {
          setState((prev) => ({
            ...prev,
            translation: state.translation + 0.05,
          }));
        }}
      >
        +
      </Button>
      <Button
        variant="contained"
        onClick={() => {
          setState((prev) => ({
            ...prev,
            translation: state.translation - 0.05,
          }));
        }}
      >
        -
      </Button>
      <Box style={{ color: "white" }}>{state.translation.toFixed(2)}</Box>
      <Canvas
        width={window.innerWidth}
        height={height}
        onPointerDown={handlePointerDown}
        onPointerUp={handlePointerUp}
        onPointerMove={handlePointerMove}
        onPointerCancel={handlePointerCancel}
        style={{ backgroundColor: "white" }}
      />
    </Box>
  );
};
