import React, { useRef, useEffect, useCallback, useState } from "react";
import { useSharedState } from "../context";

export const Canvas = (
  props: JSX.IntrinsicAttributes &
    React.ClassAttributes<HTMLCanvasElement> &
    React.CanvasHTMLAttributes<HTMLCanvasElement>
) => {
  const canvaRef = useRef<HTMLCanvasElement>(null);
  const [state] = useSharedState();

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, frameCount: number) => {
      ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      ctx.fillText("lala", 100, 100);
      if (state.draw_controls) {
        ctx.beginPath();
        ctx.arc(state.startX, state.startY, 40, 0, 2 * Math.PI);
        ctx.stroke();

        ctx.font = "10";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";
        ctx.fillText(state.direction, state.startX, state.startY);

        ctx.beginPath();
        ctx.arc(state.currentX, state.currentY, 20, 0, 2 * Math.PI);
        ctx.stroke();
      }
    },
    [state.direction, state.draw_controls, state.currentX, state.currentY]
  );

  useEffect(() => {
    const canvas = canvaRef.current;
    const context = canvas?.getContext("2d");
    if (context) {
      context.translate(state.translation, state.translation);
    }
  }, [canvaRef.current, state.translation]);

  useEffect(() => {
    const canvas = canvaRef.current;
    const context = canvas?.getContext("2d");

    let frameCount = 0;
    let animationFrameId = 0;

    //Our draw came here
    const render = () => {
      frameCount++;
      draw(context!, frameCount);
      animationFrameId = window.requestAnimationFrame(render);
    };
    render();

    return () => {
      window.cancelAnimationFrame(animationFrameId);
    };
  }, [draw]);

  return (
    <canvas
      ref={canvaRef}
      {...props}
      style={{ ...props.style, touchAction: "none" }}
    />
  );
};
